<template>
    <div class="workDelay" >
        <navigation-bar pageName="内部工单审批" @onLeftClick="goBack">
        </navigation-bar>
        <div class="tabs">
          <div class="tab" @click="changeList(0, true)">
            <div class="tab-item">
              <span :style="{color: activeTab == 0 ? '#333333' : '#9A9A9A'}">延时申请</span>
              <!-- <div class="dot"></div> -->
            </div>
            <div class="border"></div>
            <div class="border-box" v-show="activeTab == 0"></div>
          </div>
          <div class="tab" @click="changeList(1, true)">
            <div class="tab-item">
              <span :style="{color: activeTab == 1 ? '#333333' : '#9A9A9A'}">修改分类申请</span>
              <!-- <div class="dot"></div> -->
            </div>
            <div class="border-box" v-show="activeTab == 1"></div>
          </div>
        </div>
        <div class="workList">
          <van-pull-refresh v-model="isRefresh" @refresh="changeList(activeTab)">
            <div class="workContent" v-show="workList.length > 0">
              <van-list
                v-model="loading"
                :finished="finished"
                :finished-text="finishedText"
                :immediate-check="false"
                @load="loadMore">
                <div class="item" v-for="(item,index) in workList" :key="index" @click="goDetail(item)">
                    <div class="title">
                        <div class="top">
                            <img :src="workIcon" alt="">
                            <div class="building">{{item.areaName}} {{item.buildName}} {{item.unitNo ? item.unitNo + "- " : ''}}{{item.roomName}}</div>
                        </div>
                        <div class="fontGreen status" v-if="item.approvalStatus == 0">待审批</div>
                        <div class="status fontBlack" v-if ="item.approvalStatus == 1">已审批</div>
                    </div>
                    <div class="content">
                        <div class="img-box">
                            <img v-bind:src="(item.picUrls && item.picUrls.length > 0) ? item.picUrls[0] : defaultImg" alt="" v-on:error.once="showErrorImg($event)">
                        </div>
                        <div class="dis-box">
                            <div class="redTip">#{{item.labelName}}#</div>
                            <div class="content-dis">{{item.reason}}</div>
                        </div>
                    </div>
                </div>
              </van-list>
            </div>
            <div class="no-list" v-show="workList.length == 0 && !isEnterNLoading ">
              <img :src="noSourceImg" alt="">
              <div>暂无工单</div>
            </div>
          </van-pull-refresh>
        </div>
    </div>
</template>

<script>
import { getInterDelayList } from "../../services/internalWorkDelayService";
import { store } from "../../utils/store";
import defaultImg from "@/assets/imgs/workPlan/imgExample.png";
import finishImg from "@/assets/imgs/workPlan/finishLogo.png";
import noSourceImg from "@/assets/imgs/workPlan/noSource.png";
import leftIcon from "@/assets/imgs/workPlan/left_black.png";
import workIcon from "@/assets/imgs/checkHouse/work.png";
import delayIcon from "@/assets/imgs/workDelay/delayLamp.png";
import NavigationBar from "../title/NavigationBar";
export default {
  name: "workDelayInside",
  components: { NavigationBar },
  data() {
    return {
      activeTab: 0,

      // 上拉加载是否处于加载状态
      loading: false,
      isRefresh: false,

      isEnterNLoading: true,

      // 上拉加载是否加载完成
      finished: false,
      finishedText: "我是有底线的",

      // 工单列表
      workList: [],

      workListFunction: [this.getDelayList, this.getCategoryList],

      page: 1,
      rows: 10,

      // 图片
      defaultImg: defaultImg,
      finishImg: finishImg,
      noSourceImg: noSourceImg,
      leftIcon: leftIcon,
      workIcon: workIcon,
      delayIcon: delayIcon,
      scrollTop: 0
    };
  },
  beforeRouteLeave(to, from, next) {
    this.scrollTop = document.querySelector(".workList").scrollTop;
    next();
  },
  activated: function() {
    var workDelayListStatus = store.get("workDelayListStatus");
    console.log(workDelayListStatus)
    //   0(使用缓存),1(定位到初始状态),2(局部刷新，刷新列表的数据)
    if (workDelayListStatus == 2) {
      document.querySelector(".workList").scrollTop = 0;
      this.changeList(this.activeTab)
      store.dele("workDelayListStatus");
    } else if (!(workDelayListStatus == undefined) && workDelayListStatus == 0) {
      document.querySelector(".workList").scrollTop = this.scrollTop;
      store.dele("workDelayListStatus");
    } else {    // workDelayListStatus为undefined或者是1
      document.querySelector(".workList").scrollTop = 0;
      store.dele("workDelayListStatus");
      this.resetData();
    }
  },
  methods: {

    showErrorImg: function(event) {
      event.currentTarget.src = this.defaultImg;
    },

    /**
     * tab切换事件
     */
    changeList: function(type, fromTabTouch = false) {
      if (fromTabTouch && this.activeTab == type) {
        console.log("click for the same tab, ignore.");
        return;
      }
      this.activeTab = type;
      this.workList = [];
      this.page = 1;
      this.workListFunction[type](fromTabTouch);
    },

    /**
     * 获取延时申请列表
     */
    getDelayList: function(fromTabTouch = false) {
      if (!this.isEnterNLoading && !fromTabTouch) {
        this.loading = true;
        this.isRefresh = true;
      }
      var data = {
        userCode: store.get("userInfo").USER_CODE,
        reportPhone: store.get("userInfo").PHONE,
        approvalType: "0",
        approvalStatus: "0",    // 1是未审批，2为已审批，只请求未审批的
        page: this.page,
        rows: this.rows,
        areaCode: store.get("userInfo").AREA_CODE,
        builCode: ""
      };

      getInterDelayList(data).then(
        res => {
          if(res.resultCode == 0) {
            this.setWorkList(res.data);
          } else {
            this.setErrorInfo(res.msg);
          }
          this.resetEnterLoadingStatus();
        },
        error => {
          this.setErrorInfo(error);
          this.resetEnterLoadingStatus();
        }
      )
      .catch(err => {
        this.setErrorInfo(error);
        this.resetEnterLoadingStatus();
      });
    },

    /**
     * 获取修改分类申请列表
     */
    getCategoryList: function(fromTabTouch = false) {
      if (!this.isEnterNLoading && !fromTabTouch) {
        this.loading = true;
        this.isRefresh = true;
      }
      var data = {
        userCode: store.get("userInfo").USER_CODE,
        reportPhone: store.get("userInfo").PHONE,
        approvalType: "1",
        approvalStatus: "0",    // 1是未审批，2为已审批，只请求未审批的
        page: this.page,
        rows: this.rows,
        areaCode: store.get("userInfo").AREA_CODE,
        builCode: ""
      };

      getInterDelayList(data).then(
        res => {
          if(res.resultCode == 0) {
            this.setWorkList(res.data);
          } else {
            this.setErrorInfo(res.msg);
          }
          this.resetEnterLoadingStatus();
        },
        error => {
          this.setErrorInfo(error);
          this.resetEnterLoadingStatus();
        }
      )
      .catch(err => {
        this.setErrorInfo(error);
        this.resetEnterLoadingStatus();
      });
    },

    resetEnterLoadingStatus() {
      if (this.isEnterNLoading) {
        //setTimeout(() => {
          this.isEnterNLoading = false;
        //}, 200);
      }
    },

    /**
     * 设置错误的处理信息
     */
    setErrorInfo(error) {
      this.loading = false;
      this.isRefresh = false;
      this.$toast(error);
      this.finished = true;
      this.finishedText = "";
    },

    /**
     * 设置展示的工单列表信息
     */
    setWorkList(res) {
      this.loading = false;
      this.isRefresh = false;
      this.finished = res.total <= this.page * this.rows;
      if (this.page == 1) {
        this.workList = res.list ? res.list : [];
      } else {
        this.workList = this.workList.concat(
          res.list ? res.list : []
        );
      }
      if (this.page == 1 && this.finished) {
        this.finished = true;
        this.finishedText = "";
      }
    },

    /**
     * 上拉加载
     */
    loadMore: function() {
      this.page++;
      this.getWorkList();
    },

    /**
     * 获取订单列表
     */
    getWorkList: function() {
      if(this.activeTab == 0) {
        this.getDelayList();
      } else {
        this.getCategoryList();
      }
    },

    /**
     * 页面返回事件
     */
    goBack: function() {
      this.$router.back(-1);
    },

    /**
     * 跳转到工单详情页面
     */
    goDetail: function(item) {
      this.$router.push({
        name: "delayDetailInside",
        params: {
          complCode: item.complCode,
          uuid: item.uuid
        }
      });
    },

    /**
     * 初始化
     */
    resetData: function() {
      this.activeTab = store.get("insideWorkDelayTab") ? store.get("insideWorkDelayTab") : 0;
      store.dele("insideWorkDelayTab")
      this.loading = false;
      this.finished = false;
      this.finishedText = "我是有底线的";
      this.workList = [];
      this.page = 1;
      this.rows = 10;
      this.isEnterNLoading = true;
      this.getWorkList();
    }
  },
  computed: {}
};
</script>
